@import '../../styles/variables.scss';

.randomchar {
    display: grid;
    grid-template-columns: 1fr 1fr;
    box-shadow: 5px 5px 20px rgb(0 0 0 / 25%);

    &__block {
        display: grid;
        grid-template-columns: 180px auto;
        column-gap: 30px;
        padding: 40px 35px;
    }

    &__info {
        display: grid;
        grid-template-rows: minmax(29px, auto) 90px 38px;
        row-gap: 10px;
        padding-top: 3px;
    }

    &__image {
        width: 180px;
        height: 180px;
        object-fit: cover;
    }

    &__name {
        font-weight: bold;
        font-size: 22px;
        line-height: 29px;
        text-transform: uppercase;
    }

    &__descr {
        font-size: 14px;
        line-height: 18px;
    }

    &__btns a:nth-child(1) {
        margin-right: 30px;
    }

    &__static {
        padding: 40px 35px;
        background-color: $dark;
        position: relative;
        background-image: url('../../resources/img/mjolnir.png');
        background-repeat: no-repeat;
        background-position: bottom 14px right -37px;
        button {
            margin-top: 13px;
        }
    }

    &__title {
        font-weight: bold;
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.045em;
        color: #FFFFFF;
        &--second {
            margin-top: 33px;
        }
    }
}